#settings {
  .nav-item {
    padding: 0;
  }

  .pills-primary .nav-link {
    background-color: darken(#fff, 2%) !important;
    border-color: #77bd43;
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: #212529;
    text-align: left;
    box-shadow: none;

    i {
      padding-left: 0.5rem;
    }

    &:hover {
      background-color: darken(#fff, 5%) !important;
    }

    &.active {
      background-color: darken(#fff, 10%) !important;
      border-left: 5px #77bd43 solid;
    }
  }

  .tab-content {
    padding: 0;
    text-align: left;

    .font-weight-bold {
      margin-bottom: 0.4rem;
      padding-bottom: 0;
      border-bottom: 3px #77bd43 solid;
      display: inline-block;
      margin-top: 1rem;
    }
  }

  .form-check-input[type="checkbox"].filled-in:checked + label::after,
  label.btn input[type="checkbox"].filled-in:checked + label::after {
    background-color: #77bd43;
    border: 2px solid #77bd43;
  }

  .personal-data {
    .form-check {
      padding-left: 0 !important;
      margin-top: 0.4rem;
    }
  }
}

